body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #2980B9;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #FFFFFF, #6DD5FA, #2980B9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 1.5em;
}

.calcWrapper {
  background-color: gray;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
  width: 300px;
  height: 440px;
  margin: 100px auto;
  border: 1px solid gray;
  border-radius: 5px;
  box-shadow: 10px 10px 15px 0px rgba(0,0,0,0.48);
}

#display {
  background: #16222A;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #3A6073, #16222A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  width: 100%;
  height: 70px;
  margin: 0 auto;
  text-align: right;
  padding: 10px;
}

.row {
  width: 100%;
  margin: auto;
}

#divide, #multiply, #add, #subtract, #clear {
  background: #1488CC;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2B32B2, #1488CC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
}

#clear {
  width: 100%;
}

button {
    background: #606c88;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #3f4c6b, #606c88); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    color: white;
    width: 25%;
    height: 70px;
    text-decoration: none;
    border: 1px solid gray;
    margin: 0;
    cursor: pointer;
    font-size: .8em;
    transition: all .1s ease-in-out;
}

button:hover {
    border: 1px solid lightgray;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}
